import React, { useState, useEffect } from "react"
import { TextField, Button, Paper, } from "@material-ui/core"
import { useMutation } from "@apollo/client"
import { ADD_USER } from "../queries"
import { useHistory } from "react-router-dom"
import { WHITESQUARE_COLOR, BLACKSQUARE_COLOR } from "../utils/constants"

const RegisterForm = () => {
  const [username, setUsername] = useState("")
  const [password, setPassword] = useState("")
  const [confirmation, setConfirmation] = useState("")
  const [tag, setTag] = useState("")
  const history = useHistory()

  const [addUser, addedResult] = useMutation(ADD_USER, {
    onError: err => {
      console.log(err.graphQLErrors[0].message)
      window.alert(err.graphQLErrors[0].message)
    }
  })

  const onSubmit = event => {
    event.preventDefault()

    const user = { username, password, tag }
    addUser({ variables: { user } })
  }

  useEffect(() => {
    if (addedResult.data) {
      setUsername("")
      setPassword("")
      setTag("")

      window.alert("Kyttj luotu onnistuneesti.")
      history.push("/")
    }
  }, [addedResult.data, history])

  const style = {
    background: `linear-gradient(135deg, rgb(${WHITESQUARE_COLOR}), rgb(${BLACKSQUARE_COLOR}))`,
    padding: "20px",
  }

  return (
    <Paper style={style}>
      <h2>Luo uusi kyttj</h2>
      <form onSubmit={onSubmit}>
        <div>
          <TextField
            label="Kyttjnimi"
            onChange={e => {setUsername(e.target.value)}}
            error={username.length !== 0 && username.length < 2}
            helperText={
              username.length !== 0 && username.length < 2
                ? "Liian lyhyt"
                : "Kyttjnimesi ei ny muille pelaajille."
            }
            required={true}
          />
        </div>
        <div>
          <TextField
            label="Pelinimi"
            onChange={e => {setTag(e.target.value)}}
            helperText="Nimi joka nkyy muille pelaajille."
            required={true}
          />
        </div>
        <div>
          <TextField
            label="Salasana"
            type="password"
            onChange={e => {setPassword(e.target.value)}}
            error={password.length < 8 && password !== ""}
            helperText={
              password.length < 8 && password !== ""
                ? "Liian lyhyt"
                : null
            }
            required={true}
          />
          <div
            style={{
              paddingTop: 20,
              paddingBottom: 10,
            }}
          >
            <strong>l kyt samaa salasanaa, joka sinulla on muilla nettisivuilla. </strong>
            Olen vain opiskelija, ja tm on ensimminen nettiprojektini, joten en voi taata sivuston tietoturvallisuutta.
          </div>
        </div>
        <div>
          <TextField
            label="Salasana uudelleen"
            type="password"
            onChange={e => {setConfirmation(e.target.value)}}
            error={confirmation !== password}
            helperText={confirmation !== password ? "Salasanat eivt vastaa toisiaan." : null}
            required={true}
          />
        </div>
        <div>
          <Button variant="contained" color="primary" type="submit">
            Luo kyttj
          </Button>
        </div>
      </form>
    </Paper>
  )
}

export default RegisterForm